'use client'

import styles from './MenuItens.module.css'
import React, { useEffect, useState, useContext } from "react";
import Slider from "react-slick";
import { CarrinhoContext } from "@/app/carrinho/components/CarrinhoContext";
import { Pizza } from "@/app/carrinho/components/Main";

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

export default function MenuItens() {
    const carrinhoContext = useContext(CarrinhoContext);

    if (!carrinhoContext) {
        throw new Error("CarrinhoContext is undefined.");
    }

    const { adicionarPizza } = carrinhoContext;
    const [pizzas, setPizzas] = useState<Pizza[]>([]);

    useEffect(() => {
        fetch("http://localhost:3000/cardapio")
            .then((res) => res.json())
            .then((data) => {
                console.log("Dados recebidos:", data);
                setPizzas(data);
            });
    }, []);
    

    const settings = {
        dots: true,
        infinite: false,
        speed: 500,
        slidesToShow: 2,
        slidesToScroll: 1,
    };

    return (
        <Slider {...settings}>
            {pizzas.map((pizza) => (
                <div key={pizza.id} className={styles.slide}>
                    <div className={styles.postPizza}>
                        <img src={`http://localhost:3000${pizza.imagem}`} alt={pizza.nome} />
                        <h1>{pizza.nome}</h1>
                        <p>{pizza.descricao}</p>
                        <h2>{pizza.preco}</h2>
                        <button onClick={() => adicionarPizza(pizza)}>
                            <h1>Pedir agora</h1>
                        </button>
                    </div>
                </div>
            ))}
        </Slider>
    );
}
